@import '../../stylesheets/variables';
  
  table.calendar {
    border-collapse: collapse;
  }
  
  table.calendar thead {
    color: black;
    margin-bottom: 3px;
    border-bottom: 2px solid white
  }
  
  table.calendar thead th {
    font-weight: normal;
    padding: 10px 3px;
  }
  
  table.calendar thead th.bolder {
    font-weight: bold;
  }
  
  table.calendar tbody {
    font-size: 0.8em;
  }
  
  table.calendar td {
    text-align: center;
    padding: 8px;
    cursor: pointer;
    border: 1px solid rgba(185, 185, 185, 0.13);
    background-color: white;
    min-width: 15px;
  }
  
  table.calendar tr:last-child td {
    border-bottom: none;
  }
  
  table.calendar td.month-name {
    font-weight: bold;
    text-align: left;
    cursor: default;
    border-left: none;
  }
  
  table.calendar td.prev-month,
  table.calendar td.next-month {
    color: transparent;
    cursor: default;
    pointer-events: none;
    background-image: url('../../images/bg-calendar.png');
  }
  
  table.calendar td.week-separator {
    pointer-events: none;
    padding: 0;
    width: 8px;
    min-width: 0;
    background: url('../../images/bg-calendar.png');
  }
  
  table.calendar td.bolder {
    font-weight: bold;
  }
  
  table.calendar td.selected {
    background-color: rgb(255, 255, 255);
    color: black;
  }
  
  div.calendar-controls {
    margin: 5px auto;
    display: table;
    font-size: 25px;
    line-height: 35px;
  }
  
  div.calendar-controls div {
    display: inline;
  }
  
  div.calendar-controls .current-year {
    margin: 0 30px;
    font-weight: bold;
  }
  
  div.calendar-controls .control {
    font-weight: bolder;
    color: #323232;
    font-size: 0.8em;
    cursor: pointer;
  }
  
  div.calendar-controls .today {
    position: absolute;
    right: 15px;
    line-height: 35px;
    font-size: 0.6em;
    text-transform: uppercase;
  }
  
  /* Demo */
  div#calendar {
    position: relative;
    padding: 15px;
    border-radius: 15px;
    border: 1px solid rgb(216, 216, 216);
    background-color: white;
    overflow: hidden;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    box-shadow: 5px 5px 20px #cacaca;
    margin: 0 auto;
  }  
 
  h2,h3,h4,h5 {
    text-align: center;
    color: #404040;
  }
  
  h1 {
    text-align: center;
    color: #B10909;
  }
  
  a {
    color: #B10909;
    font-weight: bolder;
    text-decoration: none;
  }
  
  a.demoLink {
    text-decoration: underline;
  }
  
  div.options {
    border: 1px solid #B9B9B9;
    border-radius: 5px;
    padding: 10px 15px;
    margin-top: 30px;
  }
  
  div.options select {
    margin-left: 10px;
  }

  table.calendar td.periods {
    background-color: $primaryColor200;
  }

   /* Selected range */
   table.calendar td.range {
    background-color: $selectedColor100;
    font-weight: bold;
    color: white;
  }
  table.calendar td.range-left {
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    background: $selectedColor400;
  }
  
  table.calendar td.range-right {
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
    background: $selectedColor400;
  }